@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

@keyframes rotation {
    from {
        transform: rotate(0);
    }
    to {
        transform: rotate(360deg);
    }
}
  
.rotation {
    animation: rotation 5s linear infinite;
}

.quantity {
    box-sizing: border-box;
    padding: 0 5.78vw 0 13.8vw;
    height: 10vw;
    background-image: url(./images/2\ переход.jpg);
    background-size: 100% 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr) 1.1fr;
    gap: 11vw;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 0.94vw;
    line-height: 1.15vw;
    color: #FFFFFF;
}

.quantity__column {
    align-self: center;
}

.quantity__column_num_two {
    margin-bottom: 2vw;
}

.quantity__number {
    width: 12vw;
    height: 5.2vw;
    background-size: 100% 100%;
}

.quantity__number_num_one {
    background-image: url(./images/42.svg);
    background-size: auto 100%;
}

.quantity__number_num_two {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 4.7vw;
    line-height: 5.73vw;
}

.quantity__number_num_three {
    background-image: url(./images/3047.svg);
    display: flex;
}

.quantity__zero {
    display: block;
    width: 4vw;
    height: 4vw;
    background-image: url(./images/0.svg);
    background-size: 100% 100%;
    position: relative;
}

.quantity__zero_num_one {
    left: 5.2vw;
    top: 0.7vw;
}

.quantity__zero_num_two {
    background-image: url();
    width: 4vw;
    height: 4vw;
    left: 4.8vw;
    top: 1.1vw;
}

.quantity__zero_num_three {
    left: 2vw;
    top: 0.6vw;
}

.quantity__paragraph {
    position: relative;
    bottom: 0.5vw;
    font-weight: 300;
    width: 10vw;
}

.quantity__paragraph_column_first {
    font-style: normal;
    font-weight: 300;
    font-size: 36px;
    line-height: 44px;
    color: #FFFFFF;
    padding-left: 5vw;
}

.margin-left {
    margin-left: -0.6vw;
}

.quantity__map {
    display: block;
    width: 8vw;
    height: 7vw;
    background-image: url(./images/Карта.svg);
    background-size: 100% 100%;
    transform: rotate(10deg);
    position: relative;
    left: 5vw;
    bottom: 0.3vw;
}