.master {
    box-sizing: border-box;
    padding-top: 4.43vw;
    height: 57.2vw;
    background-image: url(./images/Титульник.jpg);
    background-size: cover;
    position: relative;
}

.master__social-icons {
    list-style-type: none;
    box-sizing: border-box;
    width: 13.54vw;
    height: 4.95vw;
    background: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(0.31vw);
    border-radius: 1.04vw 1.04vw 0 0;
    transform: matrix(1, 0, 0, -1, 0, 0);
    float: right;
    margin-right: 7.9vw;
    display: flex;
    justify-content: space-between;
    padding: 0 1.82vw;
}

.master__social-icon {
    display: block;
    width: 1.82vw;
    height: 1.82vw;
    background-size: 100% 100%;
    position: relative;
    top: 1.3vw;
    transform: scale(1, -1);
    opacity: 0.6;
    transition: opacity 0.5s;
}

.master__social-icon:hover {
    opacity: 1;
}

.master__social-icon_type_instagram {
    background-image: url(./images/instagram.svg);
}

.master__social-icon_type_telegram {
    background-image: url(./images/telegram.svg);
}

.master__social-icon_type_vk-circled {
    background-image: url(./images/vk-circled.svg);
}

.master__social-icon_type_facebook {
    background-image: url(./images/facebook.svg);
}

.title {
    width: 19vw;
    margin-top: 5.78vw;
    margin-left: 5.2vw;
}

.title__paragraph {
    font-style: normal;
    font-weight: 300;
    font-size: 0.94vw;
    line-height: 1.09vw;
    text-align: right;
    color: #FFFFFF;
    opacity: 0.8;   
    padding-bottom: 0.5vw;
}

.title__name {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 2.6vw;
    line-height: 2.97vw;
    display: flex;
    align-items: center;
    letter-spacing: 0.8;
    color: #FFFFFF;    
}

.values {
    margin-top: 3.07vw;
    margin-left: 8.33vw;
    width: 69.65vw;
    height: 16.82vw;
    background-image: url(./images/Ценности.svg);
    background-size: 100% 100%;
}

.values__list {
    list-style-type: none;
    display: flex;
    position: relative;
}

.values__item {
    height: 5.2vw;
    width: 17vw;
    background-size: 100% 100%;
    position: absolute;
    transition: transform 0.5s;
}

.values__item:hover {
    transform: scale(1.1);
    -webkit-filter: drop-shadow(0 0 3px #FFF);
            filter: drop-shadow(0 0 3px #FFF);
}

.values__responsibility {
    top: 4vw;
    left: 0;
    background-image: url(./images/ценности/responsibility.svg);
}

.values__not-indifferent {
    top: 10vw;
    left: 22vw;
    background-image: url(./images/ценности/not-indifferent.svg);
}

.values__respect {
    top: 1vw;
    left: 34vw;
    background-image: url(./images/ценности/respect.svg);
}

.values__knowledge {
    top: 10vw;
    left: 45.5vw;
    background-image: url(./images/ценности/knowledge.svg);
}

.values__team {
    top: 3vw;
    left: 56vw;
    background-image: url(./images/ценности/team.svg);
}

.news {
    box-sizing: border-box;
    color: #fff;
    background: linear-gradient(90deg, rgba(67, 108, 153, 1), rgba(9, 26, 86, 0.19));
    backdrop-filter: blur(0.2vw);
    width: 30vw;
    height: 16vw;
    border-radius: 1.05vw 1.05vw 0 0;
    position: relative;
    bottom: 0;
    left: 6.25vw;
    top: 3.72vw;
    padding: 0.625vw 1.2vw;
}

.news_title {
    font-style: normal;
    font-weight: 300;
    font-size: 1.25vw;
    line-height: 1.51vw;
    color: #FFFFFF;
    padding-bottom: 0.6vw;
}

.news_card {
    display: flex;
}

.news_img {
    height: 7.8vw;
    border-radius: 1.5vw;
}

.news_subtitle {
    font-family: 'Caveat', cursive;
    font-style: normal;
    font-weight: bold;
    font-size: 1.875vw;
    line-height: 2.34vw;
    padding-left: 0.7vw;
}

.news_paragraph {
    font-style: normal;
    font-weight: 400;
    font-size: 0.93vw;
    line-height: 1.09vw;
    letter-spacing: 0.1vw;
    color: #FFFFFF;
    padding: 0.7vw;
}

.news_paragraph_type_date {
    font-style: normal;
    font-weight: 300;
    font-size: 0.94vw;
    line-height: 1.09vw;
    color: #FFFFFF;
}

.title_position_bottom {
    width: 20.5vw;
    position: absolute;
    bottom: 6vw;
    right: 6vw;
}

.title__name_align_right {
    text-align: right;
}