@keyframes rotation {
    from {
        transform: rotate(0);
    }
    to {
        transform: rotate(360deg);
    }
}
  
.rotation {
    animation: rotation 5s linear infinite;
}

.separator {
    box-sizing: border-box;
    padding: 0 5.78vw 0 13.8vw;
    height: 10vw;
    background-image: url(./images/Переход.jpg);
    background-size: 100% 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr) 1.1fr;
    gap: 11vw;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 0.94vw;
    line-height: 1.15vw;
    color: #FFFFFF;
}

.separator__column {
    align-self: center;
}

.separator__column_num_two {
    margin-bottom: 2vw;
}

.separator__number {
    width: 12vw;
    height: 5.2vw;
    background-size: 100% 100%;
}

.separator__number_num_one {
    background-image: url(./images/532.svg);
}

.separator__number_num_two {
    width: 13.96vw;
    height: 5.73vw;
    background-image: url(./images/1898.svg);
}

.separator__number_num_three {
    background-image: url(./images/347.svg);
}

.separator__zero {
    display: block;
    width: 4vw;
    height: 4vw;
    background-image: url(./images/0.svg);
    background-size: 100% 100%;
    position: relative;
}

.separator__zero_num_one {
    left: 5.2vw;
    top: 0.7vw;
}

.separator__zero_num_two {
    background-image: url(./images/0-more.svg);
    width: 4vw;
    height: 4vw;
    left: 4.8vw;
    top: 1.1vw;
}

.separator__zero_num_three {
    left: 2.7vw;
    top: 0.6vw;
}

.separator__paragraph {
    position: relative;
    bottom: 0.5vw;
    font-weight: 300;
}

.separator__hashtag {
    position: relative;
    bottom: 0.5vw;
    color: rgba(255, 129, 37, 1);
    font-weight: 300;
}