@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');

@keyframes rotateQ {
    from {
        transform: rotate(30deg);
    }
    50% {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(30deg);
    }
}
  
.rotateQ {
    animation: rotateQ 3s linear infinite;
}

.description {
    box-sizing: border-box;
    height: 52.1vw;
    background-image: url(./images/КтоМы.jpg);
    background-size: 100% 100%;
    color: #FFFFFF;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.description__motto {
    box-sizing: border-box;
    padding: 3.65vw 0 0 8.6vw;
    display: grid;
    grid-template-columns: 1fr 4fr;
    gap: 11vw;
}

.description__title {
    font-weight: bold;
    font-size: 5.2vw;
    line-height: 95.9%;
    letter-spacing: 0.5vw;
    position: relative;
}

.description__question {
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    font-size: 13vw;
    line-height: 15.9vw;
    position: absolute;
    right: -5vw;
    top: -3.5vw;
    transform: rotate(30deg);
}

.description__column {
    align-self: end;
}

.description__our-motto {
    font-weight: 300;
    font-size: 1.87vw;
    line-height: 2.3vw;
}

.description__hashtag {
    font-family: 'Comfortaa', cursive;
    font-style: normal;
    font-weight: 300;
    font-weight: bold;
    font-size: 3.65vw;
    line-height: 4.06vw;
}

.description__may {
    font-family: 'Comfortaa', cursive;
    color: rgba(255, 129, 37, 0.9);
}

.description__about {
    display: grid;
    grid-template-columns: 0.9fr 1.1fr;
    padding: 0 8.6vw;
}

.description__team {
    font-weight: bold;
    font-size: 1.88vw;
    line-height: 2.3vw;
    margin-top: 3vw;
}

.description__number {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 10.42vw;
    line-height: 12.7vw;
    line-height: 1;
    text-shadow: -0.35vw -0.25vw 0vw rgba(255, 129, 37, 0.9);
    margin-right: 1vw;
}

.description__year {
    font-weight: bold;
    font-size: 1.88vw;
    line-height: 2.3vw;
}

.description__text {
    background: linear-gradient(97.19deg, rgba(255, 255, 255, 0.8) -3.7%, rgba(255, 255, 255, 0.328) 110.31%);
    backdrop-filter: blur(1.82vw);
    padding: 1.2vw 1.2vw 1.48vw 2.86vw;
    border-radius: 1vw 1vw 0 0;
    background-image: url(./images/кружки.png);
    background-size: 100% 100%;
    color: #1A4D86;
}

.description__subtitle {
    width: 20vw;
    font-style: normal;
    font-weight: 300;
    font-size: 1.86vw;
    line-height: 2.3vw;
}

.description__mo {
    background-color: #1A4D86;
    padding: 0 0.5vw;
    font-weight: 300;
    color: #FFFFFF;
}

.description__paragraph {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 0.94vw;
    line-height: 1.09vw;
    width: 35.6vw;
    margin: 0.5vw 0;
}

.description__span {
    color: #fff;
    background-color: #1A4D86;
    padding: 0 0.2vw;
}

.description__row {
    border-bottom: 0.2vw solid #1A4D86;
    margin-top: 1.5vw;
    padding: 0;
    font-size: 1.46vw;
}

.description__slogan {
    color: rgba(255, 129, 37, 0.9);
    background-color: #1A4D86;
    padding: 0.25vw 0.5vw 0;
    border-bottom: 0.2vw solid #1A4D86;;
    font-style: normal;
    font-weight: bold;
    font-size: 1.46vw;
    line-height: 1.77vw;
}

.description__list {
    padding-top: 1vw;
    list-style-type: none;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 0.94vw;
    line-height: 1.09vw;
    width: 38vw;
}

.description__item {
    padding: 0.25vw 0;
}