.header {
  width: 100vw;
  background-color: #FFF;
  font-size: 0.94vw;
  display: grid;
  grid-template-columns: 1fr 6.44fr;
  gap: 4vw;
  box-sizing: border-box;
  position: fixed;
  z-index: 1;
}

.logo {
  background-color: rgba(26,77,134, 0.9);
  clip-path: polygon(
    80% 0,
    100% 50%,
    80% 100%,
    0 100%,
    0 0
  );
  border-bottom: 1px solid rgba(26,77,134, 0.9);
}

.logo__link {
  box-sizing: border-box;
  margin-top: 0.1vw;
  display: block;
  width: 4.5vw;
  height: 100%;
  background-image: url(./images/logo-page.svg);
  background-size: 100% 100%;
  float: right;
  margin-right: 2vw;
}

.navigation__list {
  min-height: 4.43vw;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(6, 1fr) 3fr;
  text-align: center;
  box-sizing: border-box;
}

.navigation__item {
  align-self: center;
}

.navigation__link {
  text-decoration: none;
  color: #091A56;
  transition: color 0.5s;
}

.navigation__link:hover {
  color: rgba(255, 129, 37, 1);
}

.navigation__link_type_home {
  display: block;
  width: 1.5vw;
  height: 1.4vw;
  background-image: url(./images/home.svg);
  background-size: 100% 100%;
  margin: 0 auto;
  transition: opacity 0.5s;
}

.navigation__link_type_home:hover {
  opacity: 0.5;
}

.navigation__link_type_info {
  color: #FFF;
  background-color: rgba(26,77,134, 0.9);
  padding: 0.5vw 1vw;
  border-radius: 1vw;
  transition: background-color 0.5s;
}

.navigation__link_type_info:hover {
  background-color: #e69d45;
  color: #FFF;
}